import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';

let that;
let uid;
export default {
	data() {
		return {
			start_date: common.getDay(0, true),
			end_date: common.getDay(1, true),
			start_date_desc: '',
			end_date_desc: '',

			goodSellList: [],
		}
	},

	onLoad() {
		that = this;
		uid = uni.getStorageSync("uid");
		that.start_date_desc = that.start_date.split(" ")[0];
		that.end_date_desc = that.end_date.split(" ")[0];

		that.getdetail()
	},

	methods: {

		bindDate_startChange(e) {
			console.log(e)
			that.start_date = e.detail.value + ' 00:00:00';
			that.getdetail()
		},

		bindDate_endChange(e) {
			console.log(e)
			that.end_date = e.detail.value + ' 00:00:00';
			that.getdetail()
		},

		getdetail() {
			uni.showLoading({
				title: '加载中...'
			})

			const query = Bmob.Query("Bills");
			query.equalTo("userId", "==", uid);
			query.equalTo("type", "==", -1);
			query.equalTo("extra_type", "==", 1);
			query.equalTo("createdAt", ">=", that.start_date);
			query.equalTo("createdAt", "<=", that.end_date);
			query.order("-num");
			query.include("custom");
			query.statTo("sum", "num,total_money");
			query.statTo("groupby", "custom");
			query.statTo("order", "-_sumNum");
			query.statTo("groupcount", "true");
			query.count().then(res => {
				let count = res;
				let newArrar = [];
				if(count == 0){
					uni.hideLoading()
					return
				}
				
				for (let i = 0; i < Math.ceil(count / 500); i++) {
					query.limit(500);
					query.skip(500 * i);
					query.find().then(res => {
						for (let item of res) {
							if (item.custom && item.custom.objectId) {
								newArrar.push(item)
							}
						}
						
						if(i == Math.ceil(count / 500) - 1){
							that.goodSellList = newArrar.sort(function(a, b) {
								return b._sumNum - a._sumNum
							})
							uni.hideLoading()
						}
					});
				}
			})

		},

		//头部的options选择
		selectd(type) {
			if (type == "stocktype") {
				that.stock_checked = true;
			} else {
				that.stock_checked = false;
			}
			that.checked_option = type;
		},

		//支持预览图片
		priviewImg(imgurl) {
			uni.previewImage({
				current: imgurl,
				urls: [imgurl],
			});
		},

		//点击去到详情
		goDetail(value) {
			uni.navigateTo({
				url: "/pages/manage/custom/custom_detail/custom_detail?id=" + value
			})
		},

	}
}
